<template>
  <PopUp :flow='tylyqStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeTylyq'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("router.tylyq")}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.tyyy")}}：</span>
              </div>
              <div class="form-item-value">
                <a href="javascript:void(0)" class='resaon' :class="gzType==0?'a-active':''" @click="gzType=0">{{$t("router.reason1")}}</a>
                <a href="javascript:void(0)" class='resaon' :class="gzType==1?'a-active':''" @click="gzType=1">{{$t("router.reason2")}}</a>
                <a href="javascript:void(0)" class='resaon' :class="gzType==2?'a-active':''" @click="gzType=2">{{$t("router.reason3")}}</a>
              </div>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='stopRepeaterFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeTylyq'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { stopRepeater } from '@/api/router'
var that= {};
  export default {
    name: 'ghsb',
      data(){
          return {
            gzType:0
          }
      },
      props:{
        stopId:{
          type:[Number, String],
        },
        tylyqStatus:{
          type:Boolean
        },
        getRepeaterListFn: {
          type: Function,
          default: null
        },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
        /*停用路由*/
        stopRepeaterFn(){
          var remark;
          if(that.gzType==0){
            remark = that.$t("router.reason1");
          }else if(that.gzType==1){
            remark = that.$t("router.reason2");
          }else if(that.gzType==2){
            remark = that.$t("router.reason3");
          }
          var data={
            id:that.stopId,
            remark:remark
          }
          stopRepeater(data).then((res)=>{
            if(res.code==200 && res.success){
              that.success(that.$t('message.OperSuccess'));
              that.$emit('gettylyqStatus',!that.tylyqStatus);
              that.getRepeaterListFn();
            }
          })
          console.log(data)
        },
        closeTylyq(){
          that.$emit('gettylyqStatus',!that.tylyqStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    padding:0 155px;
    width:auto;
    .form-item{
      height:auto;
      display:flex;
      margin-top:20px;
      .form-item-name{
        // width:100px;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display:block;
          line-height:36px;
        }
      }
      .form-item-value{
        width:273px;
        display:flex;
        flex-direction: column;
        .resaon{
          display:block;
          width:205px;
          height:35px;
          line-height:35px;
          background:rgba(109,114,120,1);
          border-radius:2px;
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(255,255,255,1);
          margin-bottom:16px;
        }
        .a-active{
          background:#1890FF;
        }
      }
    }
  }
}
</style>



