<template>
  <div class="router-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe62c;</i>
      <span>{{$t("router.title")}}</span>
    </Topbar>
    <div class="router-main">
      <div class="router-choose">
        <div class="router-row">
          <div class="row-left">
            <span class="choose-title">{{$t("router.ksxz")}}:</span>
            <el-select class='select' v-model='currentDepartmentId' filterable :placeholder="$t('message.select')" @change='selectDepart'>
             <el-option
               v-for="item in departmentList"
               :key="item.departmentId"
               :label="item.name"
               :value="item.departmentId">
             </el-option>
           </el-select>
          </div>
          <div class="row-right">
            <div class="buttons">
              <a href="javascript:void(0)" @click='tjlyqStatus = !tjlyqStatus'>{{$t("router.tjly")}}</a>
            </div>
          </div>
        </div>
      </div>
      <div class="main">
        <el-table :data="repeaterList" style="width: 100%" :empty-text="$t('message.noData')">
          <el-table-column prop="id" :label="$t('router.xh')" width="100"></el-table-column>
          <el-table-column prop="repeaterName" :label="$t('router.sbmc')"></el-table-column>
          <el-table-column prop="repeaterNum" :label="$t('router.sbxh')"></el-table-column>
          <el-table-column prop="mac" :label="$t('router.mac')"></el-table-column>
          <el-table-column prop="ip" label="IP"></el-table-column>
          <el-table-column prop="departmentName" :label="$t('router.bsks')"></el-table-column>
          <el-table-column prop="roomName" :label="$t('router.azwz')"></el-table-column>
          <el-table-column prop="linkstatus" :label="$t('router.state')">
            <template slot-scope="scope">
              <span style='color:rgb(224, 32, 32)' v-if='repeaterList[scope.$index].linkStatus==1'>OFF</span>
              <span style='cursor:pointer'v-if='repeaterList[scope.$index].linkStatus==0'>ON</span>
              <span style='cursor:pointer'v-if='repeaterList[scope.$index].linkStatus==2'>{{$t("router.stop")}}</span>
            </template>
          </el-table-column>
          </el-table-column>
          <el-table-column prop="remark" :label="$t('router.remark')"></el-table-column>
          <el-table-column prop="operation" :label="$t('router.option')">
            <template slot-scope="scope">
              <span @click='openLyq(scope.row)' style='cursor:pointer;color:rgb(0, 145, 255)' v-if='repeaterList[scope.$index].linkStatus==2'>{{$t("router.enable")}}</span>
              <span @click='stopLyq(scope.row)' style='cursor:pointer'v-if='repeaterList[scope.$index].linkStatus!=2'>{{$t("router.stop")}}</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        background
        :page-size='pageSize'
        :current-page='currentPage'
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="repeaterData.records">
      </el-pagination>
    </div>
    <qylyq :qylyqStatus='qylyqStatus' v-if='qylyqStatus' :currentRepeater='currentRepeater' @getqylyqStatus='getqylyqStatus' :getRepeaterListFn='getRepeaterListFn'></qylyq>
    <tjlyq :tjlyqStatus='tjlyqStatus' v-if='tjlyqStatus' @gettjlyqStatus='gettjlyqStatus' :getRepeaterListFn='getRepeaterListFn'></tjlyq>
    <tylyq :tylyqStatus='tylyqStatus' v-if='tylyqStatus' @gettylyqStatus='gettylyqStatus' :stopId='stopId' :getRepeaterListFn='getRepeaterListFn'></tylyq>
  </div>
</template>
<script>
import { mapState,mapActions} from 'vuex'
import qylyq from "../components/router/qylyq.vue"
import tjlyq from "../components/router/tjlyq.vue"
import tylyq from "../components/router/tylyq.vue"
import Topbar from '../components/topbar'
import { getRepeaterList } from '@/api/router'
var that = {};
export default {
  name: 'router',
  data () {
    return {
      qylyqStatus:false,
      tjlyqStatus:false,
      tylyqStatus:false,
      userInfo:{},
      stopId:'',
      pageSize:that.global.pageSize,
      currentPage:that.global.currentPage,
      currentDepartmentId:'',
      departmentList:that.$store.state.monitor.departmentList,
      repeaterList:[],
      roomList:[],
      currentRepeater:{},
      repeaterData:[],
    }
  },
  beforeCreate(){
      that = this;
  },
  mounted:function(){
    that.userInfo = this.$store.state.login.userInfo;
    that.currentDepartmentId = that.departmentList[0].departmentId;
    that.getRepeaterListFn();
  },
  methods:{
    // ...mapActions("router",["getRepeater"]),
    handleCurrentChange(value){
      console.log(value)
      that.currentPage = value;
      that.getRepeaterListFn();
    },
    gettylyqStatus(tylyqStatus){
      that.tylyqStatus = tylyqStatus;
    },
    getqylyqStatus(qylyqStatus){
      that.qylyqStatus = qylyqStatus;
    },
    gettjlyqStatus(tjlyqStatus){
      that.tjlyqStatus = tjlyqStatus;
    },
    stopLyq(row){
      that.stopId = row.id;
      that.tylyqStatus = !that.tylyqStatus;
    },
    openLyq(row){
      console.log(row)
      that.currentRepeater = row;
      // that.getRepeater(row);
      that.qylyqStatus = !that.qylyqStatus;
    },

    /*更换科室*/
    selectDepart(departmentId){
      that.currentDepartmentId = departmentId;
      that.currentPage = that.global.currentPage;
      that.getRepeaterListFn();
    },
    /*获取路由器列表*/
    getRepeaterListFn(){
      var data={
        departmentId:that.currentDepartmentId,
        page:that.currentPage,
        pageSize:that.global.pageSize
      }
      getRepeaterList(data).then((res)=>{
        if(res.code==200 && res.success){
          that.repeaterData = res.data;
          that.repeaterList = res.data.list;
        }
      })
    },

  },
  components:{
    qylyq,
    tjlyq,
    tylyq,
    Topbar
  },
}
</script>
<style lang="scss">
html,body{
width:100%;
height:100%;
  .router-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .router-main{
      position:relative;
      margin:0 auto;
      width:83%;
      .router-choose{
        padding-top: 47px;
        display:flex;
        flex-direction: column;
        .router-row{
          display:flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom:23px;
          .row-left{
            width:50%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }
          }
          .row-right{
            width:50%;
            display:flex;
            flex-direction: row;
            justify-content: flex-end;
            .buttons{
              display:flex;
              flex-direction: row;
              a{
                display:block;
                padding:0 5px;
                height:35px;
                background:rgba(24,144,255,1);
                border-radius:2px;
                font-size:14px;
                font-family:PingFangSC-Regular,PingFang SC;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:35px;
                text-align:center;
                margin-right:12px;
              }
              a:last-of-type{
                margin-right:0;
              }
            }
          }
        }
      }
      .main th,.main td{
        text-align:center;
        .options{
          display:flex;
          flex-direction: row;
          justify-content: center;
          em{
            font-style: normal;
            width:1px;
            height:20px;
            display:block;
            margin:0 15px;
            background:#D8D8D8;
          }
          .iconfont{
            color:#000;
            font-size:22px;
          }
        }
      }
      .main th{
        font-size:16px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:600;
        color:rgba(0,0,0,1);
        .cell{
          word-wrap: break-word;
          word-break: keep-all;
        }
      }
      .main .cell{
          word-wrap: break-word;
          word-break: keep-all;
        }
      .main td{
        font-size:14px;
        font-family:PingFangSC-Regular,PingFang SC;
        font-weight:400;
        color:#A3A3A3;
      }
    }
  }
}
.main /deep/ .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
}
.main .el-pagination{
  display:flex;
  justify-content: flex-end;
  padding:21px 0;
  float:right;
}
</style>



