<template>
  <PopUp :flow='tjlyqStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeTjlyq'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62c;</i>
          <span class="title-span">{{$t("router.tjlyq")}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.sbmc")}}：</span>
              </div>
              <div class="form-item-value">
                <el-select class='select' v-model='newRepeater.id' filterable :placeholder="$t('message.select')" @change='selectMachineType'>
                 <el-option
                   v-for="item in machineTypeList"
                   :key="item.id"
                   :label="item.name"
                   :value="item.id">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.sbxh")}}：</span>
              </div>
              <div class="form-item-value">
                <el-select class='select' v-model='newRepeater.machineNumId' filterable :placeholder="$t('message.select')">
                 <el-option
                   v-for="item in machineNumList"
                   :key="item.id"
                   :label="item.name"
                   :value="item.id">
                 </el-option>
               </el-select>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.mac")}}：</span>
              </div>
              <div class="form-item-value">
                <el-input v-model="newRepeater.mac" class='select' placeholder="mac"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.ip")}}：</span>
              </div>
              <div class="form-item-value">
               <el-input v-model="newRepeater.ip" class='select' placeholder="ip"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name form-item-name-em">
                <span>{{$t("router.bsks")}}</span><em>：</em>
              </div>
              <div class="form-item-value">
                <el-select class='select' style='width:133px;float:left' v-model='newRepeater.departmentId' filterable :placeholder="$t('message.select')" @change='selectDepartment'>
                 <el-option
                   v-for="item in $store.state.monitor.departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
               <el-select class='select' style='width:133px;float:left' v-model='newRepeater.roomId' filterable :placeholder="$t('message.select')" @change='selectRoom'>
                 <el-option
                   v-for="item in roomList"
                   :key="item.roomId"
                   :label="item.name"
                   :value="item.roomId">
                 </el-option>
              </el-select>
              </div>
            </div>
          </div>
          <div class="form-img">
            <img src="../../assets/img/router/router.png" alt="" v-if="language=='zh'">
            <img src="../../assets/img/router/router1.png" alt="" v-if="language=='en'">
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='addRepeater'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeTjlyq'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { getMachineType,getMachineNum,getRoomsList,addRepeater } from '@/api/router'

var that= {};
  export default {
    name: 'tjlyq',
      data(){
          return {
            machineTypeList:[],
            machineNumList:[],
            newRepeater:{
              departmentId: '',
              id: '',
              machineNumId: '',
              roomId: '',
              mac:'',
              ip:''
            },
            language:that.$store.state.login.language,
            roomList:[],

          }
      },
      props:{
        tjlyqStatus:{
          type:Boolean
        },
        getRepeaterListFn: {
          type: Function,
          default: null
        },
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.getMachineTypeFn();
      },
      methods:{
        /*获取房间*/
        getRoomsListFn(){
          var data={
            departmentId:that.newRepeater.departmentId
          }
          getRoomsList(data).then((res)=>{
            if(res.code==200 && res.success){
              that.roomList = res.data;
              if(that.roomList.length!=0){
                that.newRepeater.roomId = that.roomList[0].roomId;
              }else{
                that.newRepeater.roomId = null;
              }
            }
          })
        },
        selectDepartment(departmentId){
          that.newRepeater.departmentId = departmentId;
          that.getRoomsListFn();
        },
        selectRoom(roomId){
          that.newRepeater.roomId =  roomId;
        },
        /*获取路由器设备名称*/
        getMachineTypeFn(){
          getMachineType().then((res)=>{
            if(res.code==200 && res.success){
              that.machineTypeList = res.data;
            }
          })
        },
        selectMachineType(id){
          that.newRepeater.id = id;
          that.getMachineNumFn();
        },
        /*获取路由器型号*/
        getMachineNumFn(){
          var data={
            id:that.newRepeater.id
          }
          getMachineNum(data).then((res)=>{
            if(res.code==200 && res.success){
              that.machineNumList = res.data;
              that.newRepeater.machineNumId = that.machineNumList[0].id;
            }
          })
        },
        addRepeater(){
          console.log(that.newRepeater)
          if(that.newRepeater.id==''){
            that.error(that.$t('router.err.sbmc'))
          }else if(that.newRepeater.machineNumId==''){
            that.error(that.$t('router.err.sbxh'))
          }else if(that.newRepeater.mac==''){
            that.error(that.$t('router.err.macnull'))
          }else if(that.newRepeater.ip==''){
            that.error(that.$t('router.err.ipnull'))
          }else if(that.newRepeater.departmentId==''){
            that.error(that.$t('router.err.ksnull'))
          }else if(that.newRepeater.roomId==''){
            that.error(that.$t('router.err.fjnull'))
          }else{
            var data={
              machineTypeId:that.newRepeater.machineNumId,
              mac:that.newRepeater.mac,
              departmentId:that.newRepeater.departmentId,
              roomId:that.newRepeater.roomId,
              ip:that.newRepeater.ip,
            }
            addRepeater(data).then((res)=>{
              if(res.code==200 && res.success){
                that.success(that.$t('message.OperSuccess'));
                that.closeTjlyq();
                that.getRepeaterListFn();
                that.newRepeater = {
                  departmentId: '',
                  id: '',
                  machineNumId: '',
                  roomId: '',
                  mac:'',
                  ip:''
                }
              }
            })
          }
        },
        closeTjlyq(){
          that.$emit('gettjlyqStatus',!that.tjlyqStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    width:436px;
    padding-bottom:20px;
    .form-item{
      display:flex;
      margin-top:20px;
      .form-item-name{
        width:32%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display: flex;
          align-items: center;
          justify-content: center;
          height:100%;
        }
      }
      .form-item-name-em{
        display:flex;
        justify-content: flex-end;
        span{
          text-align: right;
        }
        em{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display: flex;
          align-items: center;
          justify-content: center;
          height:100%;
          font-style: normal;
        }
      }
      .form-item-value{
        width:68%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:left;
          display:block;
          line-height:36px;
          i{
            font-style: normal;
            color:#1890FF;
          }
        }
        .select{
          float:left;
          margin-right:10px;
          width:273px;
        }
      }
    }
  }

  .form-img{
    float:left;
    margin-right:47px;
    display:flex;
    align-items: center;
    img{
      width:202px;
      float:left;
    }
    img:hover{
      transform:scale(1.5);
      -webkit-transform:scale(1.5);
     -moz-transform:scale(1.5);
     -o-transform:scale(1.5);
    }
  }
}
.form-item-value /deep/ .el-input__inner{
  height:36px;
}
</style>



