<template>
  <PopUp :flow='qylyqStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeQylyq'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62c;</i>
          <span class="title-span">{{$t("router.qylyq")}}</span>
        </div>
        <div class="content">
          <div class="form">
            <div class="sb-form form-item">
              <div class="form-item-name">
                <span>{{$t("router.sbmc")}}：</span>
              </div>
              <div class="form-item-value">
                <span>{{currentRepeater.repeaterName}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.sbxh")}}：</span>
              </div>
              <div class="form-item-value">
                <span>{{currentRepeater.repeaterNum}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.mac")}}：</span>
              </div>
              <div class="form-item-value">
                <span>{{currentRepeater.mac}}</span>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.ip")}}：</span>
              </div>
              <div class="form-item-value">
               <el-input v-model="qyRepeater.ip" class='select' placeholder="ip"></el-input>
              </div>
            </div>
            <div class="form-item">
              <div class="form-item-name">
                <span>{{$t("router.syks")}}：</span>
              </div>
              <div class="form-item-value">
                <el-select class='select' style='width:133px;float:left' v-model='qyRepeater.departmentId' filterable :placeholder="$t('select')" @change='selectDepartment'>
                 <el-option
                   v-for="item in $store.state.monitor.departmentList"
                   :key="item.departmentId"
                   :label="item.name"
                   :value="item.departmentId">
                 </el-option>
               </el-select>
               <el-select class='select' style='width:133px;float:left' v-model='qyRepeater.roomId' filterable :placeholder="$t('select')" @change='selectRoom'>
                <el-option
                  v-for="item in roomList"
                  :key="item.roomId"
                  :label="item.name"
                  :value="item.roomId">
                </el-option>
              </el-select>
              </div>
            </div>
          </div>
          <div class="form-img">
            <img src="../../assets/img/router/router.png" alt="" v-if="language=='zh'">
            <img src="../../assets/img/router/router1.png" alt="" v-if="language=='en'">
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='openRepeater'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeQylyq'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import { getRoomsList,openRepeater } from '@/api/router'

var that= {};
  export default {
    name: 'qylyq',
      data(){
          return {
            qyRepeater:{
              ip:that.currentRepeater.ip,
              departmentId:that.currentRepeater.departmentId,
              roomId:that.currentRepeater.roomId,
            },
            roomList:[],
            language:that.$store.state.login.language,
          }
      },
      props:{
        qylyqStatus:{
          type:Boolean
        },
        getRepeaterListFn: {
          type: Function,
          default: null
        },
        currentRepeater:{
          type:Object
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        console.log(that.currentRepeater)
        that.getRoomsListFn();
      },
      methods:{
        /*获取房间*/
        getRoomsListFn(){
          var data={
            departmentId:that.qyRepeater.departmentId
          }
          getRoomsList(data).then((res)=>{
            if(res.code==200 && res.success){
              that.roomList = res.data;
              if(that.roomList.length!=0){
                // that.qyRepeater.roomId = that.roomList[0].roomId;
              }

            }
          })
        },
        selectDepartment(departmentId){
          that.qyRepeater.departmentId = departmentId;
          that.getRoomsListFn();
        },
        selectRoom(roomId){
          that.qyRepeater.roomId =  roomId;
        },
        /*启用路由器*/
        openRepeater(){
          if(that.qyRepeater.ip==''){
            that.error(that.$t('router.err.ipnull'))
          }else if(that.qyRepeater.departmentId==''){
            that.error(that.$t('router.err.ksnull'))
          }else if(that.qyRepeater.roomId==''){
            that.error(that.$t('router.err.fjnull'))
          }else{
            var data={
              ip:that.qyRepeater.ip,
              departmentId:that.qyRepeater.departmentId,
              roomId:that.qyRepeater.roomId,
              id:that.currentRepeater.id
            }
            openRepeater(data).then((res)=>{
              if(res.code==200 && res.success){
                that.success(that.$t('message.OperSuccess'));
                that.$emit('getqylyqStatus',!that.qylyqStatus);
                that.getRepeaterListFn();

              }
            })
          }
        },
        closeQylyq(){
          that.$emit('getqylyqStatus',!that.qylyqStatus);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.content{
  margin:0 42px;
  display:flex;
  flex-direction: row;
  .form{
    width:436px;
    padding-bottom:20px;
    .form-item{
      height:36px;
      display:flex;
      margin-top:20px;
      .form-item-name{
        width:32%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:right;
          display: flex;
          align-items: center;
          justify-content: center;
          height:100%;
        }
      }
      .form-item-value{
        width:68%;
        span{
          font-size:16px;
          font-family:PingFangSC-Medium,PingFang SC;
          font-weight:500;
          color:rgba(51,51,51,1);
          float:left;
          display:block;
          line-height:36px;
          i{
            font-style: normal;
            color:#1890FF;
          }
        }
        .select{
          float:left;
          margin-right:10px;
          width:273px;
        }
      }
    }
  }

  .form-img{
    float:left;
    margin-right:47px;
    display:flex;
    align-items: center;
    img{
      width:202px;
      float:left;
    }
    img:hover{
      transform:scale(1.5);
      -webkit-transform:scale(1.5);
     -moz-transform:scale(1.5);
     -o-transform:scale(1.5);
    }
  }
}
.form-item-value /deep/ .el-input__inner{
  height:36px;
}
</style>



