import service from '../utils/http'
/*获取账号列表*/
export function getRepeaterList(data) {
  return service({
    url: '/repeater/repeater',
    method: 'get',
    params:data
  })
}

/*停用路由器*/
export function stopRepeater(data) {
  return service({
    url: '/repeater/stop',
    method: 'put',
    data:data
  })
}
/*根据科室获取床位*/
export function getRoomsList(data) {
  return service({
    url: '/hospital/rooms',
    method: 'get',
    params:data
  })
}
/*启用路由器*/
export function openRepeater(data) {
  return service({
    url: '/repeater/start',
    method: 'put',
    data:data
  })
}
/*获取路由器设备*/
export function getMachineType(data) {
  return service({
    url: '/repeater/machineType',
    method: 'get',
    params:data
  })
}
/*获取路由器设备型号*/
export function getMachineNum(data) {
  return service({
    url: '/repeater/machineNum',
    method: 'get',
    params:data
  })
}
/*添加路由器*/
export function addRepeater(data) {
  return service({
    url: '/repeater/repeater',
    method: 'post',
    data:data
  })
}



